<template>
  <div class="sgs-record-v2">
    <div class="wrapper">
      <div class="query-box">
        <div class="border">
          <el-form
            :model="queryParams"
            ref="queryForm"
            size="small"
            :inline="true"
            v-show="showSearch"
            label-width="68px"
          >
            <el-form-item label="模式" prop="labelmodel">
              <el-radio-group
                v-model="queryParams.labelmodel"
                style="width: 240px"
              >
                <el-radio-button
                  v-for="item in labelmodelArr"
                  :key="item.value"
                  :label="item.value"
                  >{{ item.label }}
                </el-radio-button>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="日期" prop="endtime">
              <!-- <el-input
            v-model="queryParams.endtime"
            placeholder="请选择日期"
            clearable
            @keyup.enter.native="handleQuery"
          /> -->
              <el-date-picker
                v-model="dateRange"
                type="daterange"
                align="left"
                range-separator="至"
                start-placeholder="开始日期"
                end-placeholder="结束日期"
                :picker-options="pickerOptions"
                size="mini"
                @change="dateChange"
                style="width: 240px"
                :append-to-body="false"
                value-format="timestamp"
              >
              </el-date-picker>
            </el-form-item>
            <el-form-item label="玩家" prop="mainUserid">
              <!-- <el-input
            v-model="queryParams.mainUserid"
            placeholder="请输入主位"
            clearable
            @keyup.enter.native="handleQuery"
          />
          -->
              <el-select
                v-model="queryParams.mainUserid"
                placeholder="搜索/选择玩家"
                clearable
                filterable
                style="width: 240px"
              >
                <el-option
                  v-for="item in pwUserList"
                  :key="item.userid"
                  :label="item.nickname"
                  :value="item.userid"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="武将" prop="mainGeneral">
              <!-- <el-input
            v-model="queryParams.mainGeneral"
            placeholder="请输入主将"
            clearable
            @keyup.enter.native="handleQuery"
          /> -->
              <el-select
                v-model="queryParams.mainGeneral"
                placeholder="搜索/选择武将"
                clearable
                filterable
                style="width: 240px"
              >
                <el-option
                  v-for="item in wjArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <!-- <el-form-item label="副位" prop="subUserid">
          <el-input
            v-model="queryParams.subUserid"
            placeholder="请输入副位"
            clearable
            @keyup.enter.native="handleQuery"
          />
        </el-form-item>
        <el-form-item label="副将" prop="subGeneral">
          <el-input
            v-model="queryParams.subGeneral"
            placeholder="请输入副将"
            clearable
            @keyup.enter.native="handleQuery"
          />
        </el-form-item> -->
            <el-form-item label="胜负" prop="result">
              <!-- <el-input
            v-model="queryParams.result"
            placeholder="请输入胜负"
            clearable
            @keyup.enter.native="handleQuery"
          /> -->
              <el-select
                v-model="queryParams.result"
                placeholder="选择胜负"
                clearable
                filterable
                style="width: 240px"
              >
                <el-option
                  v-for="item in resultArr"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value"
                >
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button
                type="primary"
                icon="el-icon-search"
                size="mini"
                @click="handleQuery"
                >查询</el-button
              >
              <el-button icon="el-icon-refresh" size="mini" @click="resetQuery"
                >重置</el-button
              >
              <el-button
                type="success"
                :loading="syncLoading"
                icon="el-icon-cloudy"
                size="mini"
                @click="handleSync"
                >同步</el-button
              >
              <!-- <el-button
            type="warning"
            plain
            icon="el-icon-download"
            size="mini"
            @click="handleExport"
            v-hasPermi="['sgs:pwRecord:export']"
            >导出</el-button
          > -->
            </el-form-item>
          </el-form>
          <div class="info-title">
            <span class="color-gray">[</span> 使用说明
            <span class="color-gray">]</span>
          </div>
          <ul class="info-content">
            <li>
              可查询欢乐排位、斗地主两种模式所有战绩。每天超过51局后不会覆盖旧战绩。
            </li>
            <li>
              每隔1小时自动同步最新数据。也可以点击绿色“同步”按钮，手动同步。
            </li>
            <li>右侧表格自动计算出场武将胜率，点击表头可排序。</li>
            <li>初上线可能有bug，欢迎反馈 : ）</li>
            <li>制作：埃城</li>
            <li>鸣谢：小胡</li>
          </ul>
        </div>
      </div>
      <div class="record-box">
        <el-row :gutter="40">
          <el-col :span="14">
            <div class="record-title">
              <i class="icon-record"></i>
              {{ labelmodel == "132" ? "排位" : "斗地主" }}对局：<span
                style="color: #bea879"
                >{{ pwRecordList.length }}</span
              >
              ， 胜场：<span class="color-red">{{ win }}</span
              >， 败场：<span class="color-green">{{ lost }}</span
              >， 胜率：<span class="color-blue">{{ rate }}%</span>
              <div class="record-title-btns">
                <el-switch
                  v-model="hideUsername"
                  active-text="隐藏玩家名称"
                  active-color="#bea879"
                  inactive-color="#EBEEF5"
                ></el-switch>
                <el-switch
                  class="ml-20"
                  v-model="timeOrder"
                  active-text="时间正序"
                  active-color="#bea879"
                  inactive-color="#EBEEF5"
                ></el-switch>
              </div>
            </div>
            <el-table
              v-loading="loading"
              :data="pwRecordList"
              v-if="labelmodel == '132'"
              :border="tableBorder"
            >
              <!-- <el-table-column type="selection" width="55" align="center" /> -->
              <!-- <el-table-column label="对局id" align="center" prop="gameid" /> -->
              <!-- <el-table-column label="模式" align="center" prop="labelmodel" /> -->
              <el-table-column
                label="日期"
                align="center"
                prop="endtime"
                width="160"
              >
                <template slot-scope="scope">
                  <!-- {{
                parseTimeSgs(scope.row.endtime - 8 * 3600, "{m}月{d}日 {h}:{i}")
              }} -->
                  {{
                    parseTime(
                      scope.row.endtime - 8 * 3600,
                      "{m}月{d}日 {h}:{i}",
                      "sgs"
                    )
                  }}
                </template>
              </el-table-column>
              <el-table-column
                :label="hideUsername ? '位置' : '2/4位'"
                align="center"
                prop="subUserid"
              >
                <template slot-scope="scope">
                  {{ hideUsername ? "2/4" : pwUserMap[scope.row.subUserid] }}
                </template>
              </el-table-column>
              <el-table-column label="副将" align="center" prop="subGeneral">
                <template slot-scope="scope">
                  {{ wjMap[scope.row.subGeneral] }}
                </template>
              </el-table-column>
              <el-table-column label="主将" align="center" prop="mainGeneral">
                <template slot-scope="scope">
                  {{ wjMap[scope.row.mainGeneral] }}
                </template>
              </el-table-column>
              <el-table-column
                :label="hideUsername ? '位置' : '1/3位'"
                align="center"
                prop="mainUserid"
              >
                <template slot-scope="scope">
                  {{ hideUsername ? "1/3" : pwUserMap[scope.row.mainUserid] }}
                </template>
              </el-table-column>
              <el-table-column label="胜负" align="center" prop="result">
                <template slot-scope="scope">
                  <!-- <el-tag type="success" v-if="scope.row.result == '1'">负</el-tag>
              <el-tag type="danger" v-else>胜</el-tag> -->
                  <span class="color-lightred" v-if="scope.row.result == '0'">
                    胜利
                  </span>
                  <span class="color-lightgreen" v-else>失败</span>
                </template>
              </el-table-column>
              <!-- <el-table-column
        label="操作"
        align="center"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="text"
            icon="el-icon-edit"
            @click="handleUpdate(scope.row)"
            v-hasPermi="['sgs:pwRecord:edit']"
            >详情</el-button
          >
          <el-button
            size="mini"
            type="text"
            icon="el-icon-delete"
            @click="handleDelete(scope.row)"
            v-hasPermi="['sgs:pwRecord:remove']"
            >删除</el-button
          >
        </template>
      </el-table-column> -->
            </el-table>

            <el-table
              v-loading="loading"
              :data="pwRecordList"
              :border="tableBorder"
              v-if="labelmodel == '149'"
            >
              <el-table-column label="日期" align="center" prop="endtime">
                <template slot-scope="scope">
                  <!-- {{
                parseTimeSgs(scope.row.endtime - 8 * 3600, "{m}月{d}日 {h}:{i}")
              }} -->
                  {{
                    parseTime(
                      scope.row.endtime - 8 * 3600,
                      "{m}月{d}日 {h}:{i}",
                      "sgs"
                    )
                  }}
                </template>
              </el-table-column>
              <el-table-column label="玩家" align="center" prop="mainUserid">
                <template slot-scope="scope">
                  {{ hideUsername ? "—" : pwUserMap[scope.row.mainUserid] }}
                </template>
              </el-table-column>
              <el-table-column label="选将" align="center" prop="mainGeneral">
                <template slot-scope="scope">
                  {{ wjMap[scope.row.mainGeneral] }}
                </template>
              </el-table-column>
              <el-table-column label="身份" align="center" prop="figure">
                <template slot-scope="scope">
                  <!-- <el-tag type="warning" v-if="scope.row.figure == '1'"
                    >地主</el-tag
                  >
                  <el-tag type="primary" v-else-if="scope.row.figure == '3'"
                    >农民</el-tag
                  >
                  <el-tag type="info" v-else>平局</el-tag> -->

                  <span class="color-gold" v-if="scope.row.figure == '1'">
                    地主
                  </span>
                  <span
                    class="color-lightblue"
                    v-else-if="scope.row.figure == '3'"
                  >
                    农民
                  </span>
                  <span class="color-gray" v-else>平局</span>
                </template>
              </el-table-column>
              <el-table-column label="胜负" align="center" prop="result">
                <template slot-scope="scope">
                  <!-- <el-tag type="success" v-if="scope.row.result == '1'"
                    >负</el-tag
                  >
                  <el-tag type="danger" v-else>胜</el-tag> -->

                  <span class="color-lightred" v-if="scope.row.result == '0'">
                    胜利
                  </span>
                  <span class="color-lightgreen" v-else>失败</span>
                </template>
              </el-table-column>
            </el-table>
            <el-pagination
              v-show="total > 0"
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              layout="sizes, prev, pager, next"
              :current-page="queryParams.pageNum"
              :page-sizes="[20, 100, 400, 999, 9999]"
              :page-size="queryParams.pageSize"
              :total="total"
            >
            </el-pagination>
          </el-col>
          <el-col :span="10">
            <div class="record-title">
              <i class="icon-wj"></i>
              武将胜率统计
            </div>

            <el-table
              :data="wjRateArr"
              :border="tableBorder"
              :default-sort="{ prop: 'total', order: 'descending' }"
            >
              <el-table-column prop="wjId" label="武将" align="center">
                <template slot-scope="scope">
                  {{ wjMap[scope.row.wjId] }}
                </template>
              </el-table-column>
              <el-table-column
                label="出场"
                sortable
                :sort-method="sortByTotal"
                :sort-orders="['descending', 'ascending']"
                align="center"
                prop="total"
              />
              <el-table-column
                label="胜场"
                sortable
                :sort-method="sortByWin"
                :sort-orders="['descending', 'ascending']"
                align="center"
                prop="win"
              />
              <el-table-column
                label="败场"
                sortable
                :sort-method="sortByLost"
                :sort-orders="['descending', 'ascending']"
                align="center"
                prop="lost"
              />
              <el-table-column
                label="胜率"
                sortable
                :sort-method="sortByRate"
                :sort-orders="['descending', 'ascending']"
                align="center"
                prop="rate"
              >
                <template slot-scope="scope"> {{ scope.row.rate }}% </template>
              </el-table-column>
            </el-table>
          </el-col>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
import wjMap from "./wj.json";

import axios from "axios";

export default {
  name: "PwRecord",
  data() {
    return {
      // 遮罩层
      loading: true,
      syncLoading: false,
      // 选中数组
      ids: [],
      // 非单个禁用
      single: true,
      // 非多个禁用
      multiple: true,
      // 显示搜索条件
      showSearch: true,
      // 总条数
      total: 0,
      // 排位战绩表格数据
      pwRecordList: [],
      // 弹出层标题
      title: "",
      // 是否显示弹出层
      open: false,
      // 查询参数
      queryParams: {
        pageNum: 1,
        pageSize: 9999,
        labelmodel: "132",
        sTime: null,
        eTime: null,
        mainUserid: null,
        mainGeneral: null,
        subUserid: null,
        subGeneral: null,
        result: null,
      },
      // 表单参数
      form: {},
      // 表单校验
      rules: {},
      wjArr: [],
      pwUserList: [],
      pwUserMap: {},
      wjMap: wjMap,
      resultArr: [
        { label: "胜", value: "0" },
        { label: "负", value: "1" },
      ],
      labelmodelArr: [
        { label: "排位", value: "132" },
        { label: "斗地主", value: "149" },
      ],
      dateRange: [new Date().getTime(), new Date().getTime()],
      pickerOptions: {
        shortcuts: [
          {
            text: "本月",
            onClick(picker) {
              const end = new Date();
              // 获取当前月的第一天
              const start = new Date(end.getFullYear(), end.getMonth(), 1);
              start.setTime(start.getTime());
              picker.$emit("pick", [start, end]);
            },
          },
        ],
        firstDayOfWeek: 1,
        disabledDate(date) {
          let now = Date.now();
          let range = (now - date.getTime()) / (1000 * 3600 * 24);
          return range < 0;
        },
      },
      apiUrl: "http://121.40.102.52/prod-api/",
      // apiUrl: "http://localhost/dev-api",
      win: 0,
      lost: 0,
      rate: 0,
      wjRateMap: {},
      wjRateArr: [],
      hideUsername: false,
      timeOrder: false,
      labelmodel: "132",
      tableBorder: true,
    };
  },
  created() {
    this.wjArr = [];
    for (let key in wjMap) {
      this.wjArr.push({
        label: wjMap[key],
        value: key,
      });
    }
    this.resetDate();
    axios({
      url: this.apiUrl + "/sgs/pwUser/list",
      method: "GET",
      params: { pageNum: 1, pageSize: 1000 },
    }).then((response) => {
      this.pwUserList = response.data.rows.filter((item) => item.status);
      this.pwUserList.forEach((item) => {
        this.pwUserMap[item.userid] = item.nickname;
      });
      this.getList();
    });
  },
  mounted() {},
  watch: {
    timeOrder(val) {
      this.pwRecordList.sort(this.compare("gameid", !val));
    },
  },
  methods: {
    resetDate() {
      this.dateRange = [new Date().getTime(), new Date().getTime()];
      const today = new Date(new Date().toLocaleDateString()).getTime();
      console.log(today, "today");
      this.queryParams.sTime = today / 1000 + 8 * 3600;
      this.queryParams.eTime = today / 1000 + 32 * 3600;
    },
    handleSync() {
      this.syncLoading = true;
      axios({
        url: this.apiUrl + "/sgs/pwRecord/sync",
        method: "GET",
        params: {},
      })
        .then((res) => {
          if (res.data.code == 200) {
            this.$message.success(res.data.msg);
          } else {
            this.$message(res.data.msg);
          }
        })
        .finally(() => {
          this.syncLoading = false;
        });
    },
    /** 查询排位战绩列表 */
    getList() {
      this.loading = true;
      this.pwRecordList = [];
      this.labelmodel = this.queryParams.labelmodel;
      console.log(this.dateRange, "this.dateRange");
      axios({
        url: this.apiUrl + "/sgs/pwRecord/list",
        method: "GET",
        params: this.queryParams,
      }).then((response) => {
        this.pwRecordList = response.data.rows;
        this.total = response.data.total;
        if (this.timeOrder) {
          this.pwRecordList.sort(this.compare("gameid", false));
        }
        this.loading = false;
        this.formatTableData();
      });
    },
    /** 搜索按钮操作 */
    handleQuery() {
      this.queryParams.pageNum = 1;
      this.getList();
    },
    /** 重置按钮操作 */
    resetQuery() {
      this.resetDate();
      this.$refs["queryForm"].resetFields();
      this.handleQuery();
    },
    dateChange(e) {
      console.log(e);
      this.queryParams.sTime = e[0] / 1000 + 8 * 3600;
      this.queryParams.eTime = e[1] / 1000 + 32 * 3600;
    },

    handleSizeChange(val) {
      this.queryParams.pageSize = val;
      this.getList();
    },
    handleCurrentChange(val) {
      this.queryParams.pageNum = val;
      this.getList();
    },
    // 计算总胜率
    formatTableData() {
      this.win = 0;
      this.lost = 0;
      this.rate = 0;
      this.wjRateMap = {};
      if (this.pwRecordList.length < 1) return;
      this.pwRecordList.forEach((item) => {
        if (item.result == "0") {
          this.win++;
        } else {
          this.lost++;
        }
        this.fotmatWjRate(item);
      });
      this.rate = Number((this.win / this.pwRecordList.length) * 100).toFixed(
        1
      );
      this.formatWjRateArr();
    },
    // 计算武将胜率
    fotmatWjRate(item) {
      const { mainGeneral, subGeneral, result } = item;
      if (mainGeneral && !this.wjRateMap[mainGeneral]) {
        this.wjRateMap[mainGeneral] = {
          wjId: mainGeneral,
          lost: 0,
          win: 0,
          total: 0,
          rate: 0,
        };
      }
      if (subGeneral && !this.wjRateMap[subGeneral]) {
        this.wjRateMap[subGeneral] = {
          wjId: subGeneral,
          lost: 0,
          win: 0,
          total: 0,
          rate: 0,
        };
      }
      if (result == "0") {
        mainGeneral && this.wjRateMap[mainGeneral].win++;
        subGeneral && this.wjRateMap[subGeneral].win++;
      } else {
        mainGeneral && this.wjRateMap[mainGeneral].lost++;
        subGeneral && this.wjRateMap[subGeneral].lost++;
      }
    },

    formatWjRateArr() {
      this.wjRateArr = [];
      for (let key in this.wjRateMap) {
        this.wjRateMap[key].total =
          this.wjRateMap[key].lost + this.wjRateMap[key].win;
        this.wjRateMap[key].rate = (
          (this.wjRateMap[key].win / this.wjRateMap[key].total) *
          100
        ).toFixed(1);

        this.wjRateArr.push(this.wjRateMap[key]);
      }
      console.log(this.wjRateArr, "wjRateArr");
    },

    sortByTotal(before, after) {
      return Number(before.total) - Number(after.total);
    },

    sortByWin(before, after) {
      return Number(before.win) - Number(after.win);
    },

    sortByLost(before, after) {
      return Number(before.lost) - Number(after.lost);
    },

    sortByRate(before, after) {
      return Number(before.rate) - Number(after.rate);
    },

    compare(property, desc) {
      return function (a, b) {
        var value1 = a[property];
        var value2 = b[property];
        return desc ? value2 - value1 : value1 - value2;
      };
    },

    parseTime(time, pattern, type) {
      if (arguments.length === 0 || !time) {
        return null;
      }
      const format = pattern || "{y}-{m}-{d} {h}:{i}:{s}";
      let date;
      if (typeof time === "object") {
        date = time;
      } else {
        if (typeof time === "string" && /^[0-9]+$/.test(time)) {
          time = parseInt(time);
        } else if (typeof time === "string") {
          time = time
            .replace(new RegExp(/-/gm), "/")
            .replace("T", " ")
            .replace(new RegExp(/\.[\d]{3}/gm), "");
        }
        if (typeof time === "number" && time.toString().length === 10) {
          time = time * 1000;
        }
        date = new Date(time);
      }
      const formatObj = {
        y: date.getFullYear(),
        m: date.getMonth() + 1,
        d: date.getDate(),
        h: date.getHours(),
        i: date.getMinutes(),
        s: date.getSeconds(),
        a: date.getDay(),
      };
      const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
        let value = formatObj[key];
        if (key === "a") {
          return ["日", "一", "二", "三", "四", "五", "六"][value];
        }
        if (type == "sgs") {
          if (result.length > 0 && value < 10 && key != "m" && key != "d") {
            value = "0" + value;
          }
          return value || 0;
        }
        if (result.length > 0 && value < 10) {
          value = "0" + value;
        }
        return value || 0;
      });
      return time_str;
    },
  },
};
</script>

<style lang="scss" scoped>
.sgs-record-v2 {
  .wrapper {
    display: flex;
    .query-box {
      padding: 10px;
      width: 320px;
      flex: 0;
      background: #3a3a3a;
      min-height: calc(100vh - 20px);
      .border {
        min-height: calc(100vh - 40px);
        padding-left: 10px;
        border-style: solid;
        border-width: 10px;
        border-image: url("~@/assets/img/border.png") 9 9 9 9 fill / 1 / 0
